import React, { useEffect } from 'react';

/**
 * DeferredCSS Component
 * Handles asynchronous loading of non-critical CSS files
 * Provides better error handling and loading states
 */
const DeferredCSS = ({
  stylesheets = ['/styles.css'],
  onLoad,
  onError,
  timeout = 10000 // 10 second timeout
}) => {
  useEffect(() => {
    const loadStylesheet = (href) => {
      return new Promise((resolve, reject) => {
        // Check if stylesheet is already loaded
        const existingLink = document.querySelector(`link[href="${href}"]`);
        if (existingLink && existingLink.media === 'all') {
          resolve(href);
          return;
        }

        const link = document.createElement('link');
        link.rel = 'stylesheet';
        link.href = href;
        link.media = 'print'; // Load as print media first to avoid blocking

        // Set up timeout
        const timeoutId = setTimeout(() => {
          reject(new Error(`Timeout loading stylesheet: ${href}`));
        }, timeout);

        link.onload = () => {
          clearTimeout(timeoutId);
          // Switch to all media once loaded
          link.media = 'all';
          link.onload = null;
          resolve(href);
        };

        link.onerror = () => {
          clearTimeout(timeoutId);
          reject(new Error(`Failed to load stylesheet: ${href}`));
        };

        document.head.appendChild(link);
      });
    };

    const loadAllStylesheets = async () => {
      try {
        const results = await Promise.allSettled(
          stylesheets.map(loadStylesheet)
        );

        const successful = results
          .filter(result => result.status === 'fulfilled')
          .map(result => result.value);

        const failed = results
          .filter(result => result.status === 'rejected')
          .map(result => result.reason.message);

        if (successful.length > 0 && onLoad) {
          onLoad(successful);
        }

        if (failed.length > 0) {
          console.warn('Some stylesheets failed to load:', failed);
          if (onError) {
            onError(failed);
          }
        }
      } catch (error) {
        console.error('Error loading stylesheets:', error);
        if (onError) {
          onError([error.message]);
        }
      }
    };

    // Start loading after a small delay to prioritize critical rendering
    const loadTimeout = setTimeout(loadAllStylesheets, 100);

    return () => {
      clearTimeout(loadTimeout);
    };
  }, [stylesheets, onLoad, onError, timeout]);

  // This component doesn't render anything
  return null;
};

/**
 * Hook for programmatic stylesheet loading
 */
export const useAsyncCSS = (stylesheets, options = {}) => {
  const [loadingState, setLoadingState] = React.useState({
    loading: false,
    loaded: [],
    failed: [],
    complete: false
  });

  useEffect(() => {
    if (!stylesheets || stylesheets.length === 0) return;

    setLoadingState(prev => ({ ...prev, loading: true }));

    const handleLoad = (loaded) => {
      setLoadingState(prev => ({
        ...prev,
        loaded,
        complete: true,
        loading: false
      }));
    };

    const handleError = (failed) => {
      setLoadingState(prev => ({
        ...prev,
        failed,
        complete: true,
        loading: false
      }));
    };

    // Create a temporary component to handle the loading
    const tempDiv = document.createElement('div');
    document.body.appendChild(tempDiv);

    const cleanup = () => {
      if (tempDiv.parentNode) {
        tempDiv.parentNode.removeChild(tempDiv);
      }
    };

    // Use the DeferredCSS component logic
    const loadStylesheet = (href) => {
      return new Promise((resolve, reject) => {
        const existingLink = document.querySelector(`link[href="${href}"]`);
        if (existingLink && existingLink.media === 'all') {
          resolve(href);
          return;
        }

        const link = document.createElement('link');
        link.rel = 'stylesheet';
        link.href = href;
        link.media = 'print';

        const timeoutId = setTimeout(() => {
          reject(new Error(`Timeout loading stylesheet: ${href}`));
        }, options.timeout || 10000);

        link.onload = () => {
          clearTimeout(timeoutId);
          link.media = 'all';
          link.onload = null;
          resolve(href);
        };

        link.onerror = () => {
          clearTimeout(timeoutId);
          reject(new Error(`Failed to load stylesheet: ${href}`));
        };

        document.head.appendChild(link);
      });
    };

    Promise.allSettled(stylesheets.map(loadStylesheet))
      .then(results => {
        const successful = results
          .filter(result => result.status === 'fulfilled')
          .map(result => result.value);

        const failed = results
          .filter(result => result.status === 'rejected')
          .map(result => result.reason.message);

        if (successful.length > 0) {
          handleLoad(successful);
        }

        if (failed.length > 0) {
          handleError(failed);
        }

        cleanup();
      })
      .catch(error => {
        handleError([error.message]);
        cleanup();
      });

    return cleanup;
  }, [stylesheets, options.timeout]);

  return loadingState;
};

export default DeferredCSS;
