import React, { useEffect, useState } from 'react';

/**
 * CriticalCSSLoader Component
 * Manages critical CSS loading and provides fallbacks for better performance
 * Ensures above-the-fold content is styled immediately
 */
const CriticalCSSLoader = ({
  children,
  fallbackCSS = '',
  onCriticalCSSLoad,
  enablePerformanceMonitoring = false
}) => {
  const [cssLoaded, setCSSLoaded] = useState(false);
  const [performanceMetrics, setPerformanceMetrics] = useState({});

  useEffect(() => {
    const startTime = performance.now();

    // Check if critical CSS is already inlined
    const hasCriticalCSS = document.querySelector('style[data-critical-css]') ||
                          document.querySelector('style') ||
                          document.head.innerHTML.includes('critical');

    if (hasCriticalCSS) {
      setCSSLoaded(true);

      if (enablePerformanceMonitoring) {
        const loadTime = performance.now() - startTime;
        setPerformanceMetrics(prev => ({
          ...prev,
          criticalCSSLoadTime: loadTime,
          hasCriticalCSS: true
        }));
      }

      if (onCriticalCSSLoad) {
        onCriticalCSSLoad({ success: true, loadTime: performance.now() - startTime });
      }
      return;
    }

    // If no critical CSS found, inject fallback
    if (fallbackCSS) {
      const style = document.createElement('style');
      style.setAttribute('data-critical-css-fallback', 'true');
      style.textContent = fallbackCSS;
      document.head.insertBefore(style, document.head.firstChild);

      setCSSLoaded(true);

      if (enablePerformanceMonitoring) {
        const loadTime = performance.now() - startTime;
        setPerformanceMetrics(prev => ({
          ...prev,
          criticalCSSLoadTime: loadTime,
          hasCriticalCSS: false,
          usedFallback: true
        }));
      }

      if (onCriticalCSSLoad) {
        onCriticalCSSLoad({
          success: true,
          loadTime: performance.now() - startTime,
          usedFallback: true
        });
      }
    } else {
      // No fallback available, proceed anyway
      setCSSLoaded(true);

      if (onCriticalCSSLoad) {
        onCriticalCSSLoad({
          success: false,
          error: 'No critical CSS or fallback available'
        });
      }
    }
  }, [fallbackCSS, onCriticalCSSLoad, enablePerformanceMonitoring]);

  // Monitor layout shifts if performance monitoring is enabled
  useEffect(() => {
    if (!enablePerformanceMonitoring || !cssLoaded) return;

    let clsValue = 0;
    let clsEntries = [];

    const observer = new PerformanceObserver((list) => {
      for (const entry of list.getEntries()) {
        if (!entry.hadRecentInput) {
          clsValue += entry.value;
          clsEntries.push(entry);
        }
      }

      setPerformanceMetrics(prev => ({
        ...prev,
        cumulativeLayoutShift: clsValue,
        layoutShiftEntries: clsEntries.length
      }));
    });

    try {
      observer.observe({ type: 'layout-shift', buffered: true });
    } catch (e) {
      // Layout shift observation not supported
      console.warn('Layout shift observation not supported:', e);
    }

    return () => {
      observer.disconnect();
    };
  }, [cssLoaded, enablePerformanceMonitoring]);

  // Log performance metrics in development
  useEffect(() => {
    if (process.env.NODE_ENV === 'development' &&
        enablePerformanceMonitoring &&
        Object.keys(performanceMetrics).length > 0) {
      console.log('Critical CSS Performance Metrics:', performanceMetrics);
    }
  }, [performanceMetrics, enablePerformanceMonitoring]);

  // Render children with optional loading state
  return (
    <div
      className={`critical-css-container ${cssLoaded ? 'css-loaded' : 'css-loading'}`}
      data-css-loaded={cssLoaded}
      style={{
        // Prevent flash of unstyled content
        visibility: cssLoaded ? 'visible' : 'hidden',
        transition: 'visibility 0.1s ease-out'
      }}
    >
      {children}
    </div>
  );
};

/**
 * Hook for monitoring critical CSS loading performance
 */
export const useCriticalCSSMetrics = () => {
  const [metrics, setMetrics] = useState({
    loadTime: null,
    hasCriticalCSS: null,
    usedFallback: null,
    cumulativeLayoutShift: 0,
    firstContentfulPaint: null,
    largestContentfulPaint: null
  });

  useEffect(() => {
    // Measure First Contentful Paint
    const observer = new PerformanceObserver((list) => {
      for (const entry of list.getEntries()) {
        if (entry.name === 'first-contentful-paint') {
          setMetrics(prev => ({
            ...prev,
            firstContentfulPaint: entry.startTime
          }));
        }
        if (entry.entryType === 'largest-contentful-paint') {
          setMetrics(prev => ({
            ...prev,
            largestContentfulPaint: entry.startTime
          }));
        }
      }
    });

    try {
      observer.observe({ type: 'paint', buffered: true });
      observer.observe({ type: 'largest-contentful-paint', buffered: true });
    } catch (e) {
      console.warn('Performance observation not supported:', e);
    }

    return () => {
      observer.disconnect();
    };
  }, []);

  return metrics;
};

/**
 * Utility function to get fallback critical CSS
 */
export const getFallbackCriticalCSS = () => {
  return `
    /* Minimal critical CSS fallback */
    *,*::before,*::after{box-sizing:border-box}
    html{line-height:1.15;-webkit-text-size-adjust:100%}
    body{margin:0;font-family:-apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,sans-serif;background-color:#00474f;color:#b5f5ec}
    .container{max-width:1200px;margin:0 auto;padding:0 1rem}
    @media (min-width:768px){.container{padding:0}}
    main{min-height:100vh;display:flex;flex-direction:column}
    .circle-container{display:inline-flex;align-items:center;justify-content:center;width:46px;height:46px;margin-right:1rem;border-radius:50%;background-color:#b5f5ec;flex-shrink:0}
    .player-wrapper{position:relative;width:100%;aspect-ratio:3/2;background:#00474f;border-radius:0.5rem;overflow:hidden}
    img{max-width:100%;height:auto}
    .bg-background{background-color:#00474f}
    .text-primary{color:#00474f}
    .text-secondary{color:#b5f5ec}
  `.replace(/\s+/g, ' ').trim();
};

export default CriticalCSSLoader;
